}

@media (max-width: 750px) {
    .mainGrid {
        grid-template-columns: 1fr;
    }
     
}*/

img {
    /* height: 100%;
    width: 100%; */
    object-fit: contain;
}
.card {
    /* max-width: 350px;
    max-height: 254px; */
    background-color: rgb(240, 205, 10);
    border: solid gray 1px;
    box-shadow: 0px 0px 4px 0 rgba(255,255,255,.7);
    transition: 0.3s;
    /* margin: 10px; */
    border-radius: 5px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.card:hover {
    box-shadow: 0px 0px 20px 0 rgba(255,255,255,1);
}

.cardName {
    background-color: rgb(92, 8, 8);
    width: 100%;
    margin: 0;
    border-bottom: solid gray 1px;
    padding: 5px 0px 5px 0px;
    letter-spacing: .10em;
}

h1 {
    font-weight: 600;
}

h2 {
    display: block;
    font-weight: 600;
    font-size: 1.5em;
    margin: 0;
}

h3 {
    font-weight: 100;
}
